import React, { Component } from 'react';

class PageHeader extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <section className="page_title">
                <h3>
                    <div className="container pb-2 pt-3 clearfix">

                        {
                            this.props.back_button
                                ?
                                <a href={this.props.back_button_url}>
                                    <button type="button" className="btn btn-primary float-left">{this.props.back_button_text}</button>
                                    &nbsp;
                                </a>
                                : <React.Fragment></React.Fragment>
                        }

                        <span className={this.props.rtl_font}> { this.props.title } </span>

                        {
                            this.props.import_btn
                                ? <button
                                    type="button"
                                    className="ml-3 btn btn-primary float-right"
                                    data-toggle="modal" data-target="#popupModal2"

                                >{this.props.import_text}</button>
                                : <React.Fragment></React.Fragment>
                        }

                        {
                            this.props.question_type_btn
                                ?
                                <button
                                    type="button"
                                    className="btn btn-primary float-right mr-2"
                                    data-toggle="modal" data-target="#popupModal"

                                >Add Question Type</button>
                                : <React.Fragment></React.Fragment>
                        }

                        {
                            this.props.add_button
                                ?
                                <button
                                    type="button"
                                    className="btn btn-primary float-right"
                                    data-toggle="modal" data-target="#popupModal"

                                >{this.props.add_button_text}</button>
                                : <React.Fragment></React.Fragment>
                        }

                        {
                            this.props.title ? <div className="horizontal-separator mt-3 mb-2">&nbsp;</div> : ''
                        }

                    </div>
                </h3>
            </section>
        );
    }
}

export default PageHeader;
